.main{
  padding-top: 1.25em;
  overflow: hidden;
}

.main-slider{
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      z-index: 2;
      bottom: 4.375em;
      width: 100%;
      display: flex;
      justify-content: center;
      &-bullet{
        width: .8125em;
        height: .8125em;
        background-color: #fff;
        opacity: .25;
        border-radius: 4.375em;
        margin: 0 .25em !important;
        &-active{
          width: 3em;
          background: #d9d9d9;
          opacity: 0.73;
        }
      }
    }
  }
  &__img{
    width: 100%;
    display: block;
    min-height: 100%;
    object-fit: cover;
  }
}

.info-block{
  padding-bottom: 1.5em;
  border-bottom: 1px solid #fff;
  text-align: center;
}

.bestseller{
  overflow: hidden;
  padding: 6em 0 5em;
  background-image: url(../img/bestseller-bg.jpg);
  background-size: cover;
  background-position: center;
  margin-top: -2.375em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
    gap: 1em;
    margin-top: 4.125em;
    padding: 0 2.5em;
  }
}

.product-card{
  position: relative;
  display: flex;
  flex-direction: column;
  &:before{
    content: '';
    position: absolute;
    top: -3em;
    left: 0;
    background-image: url(../img/product-bg.webp);
    background-size: 100% auto;
    background-repeat: no-repeat;
    width: 30.625em;
    height: 100%;
    pointer-events: none;
  }
  &__img{
    position: relative;
    padding-bottom: 82.7%;
    margin-bottom: 5em;
    img{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-family: $font2;
    text-align: center;
    padding: 0 1em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.375em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__price{
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2.5em;
    line-height: 120%;
  }
  &__btn{
    font-family: $font3;
    padding: 0 2em;
    height: 3em;
    border: 1px solid #fff;
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
    margin-top: .25em;
    &:hover{
      background-color: #fff;
      color: $black;
    }
    span{
      font-size: 2em;
      line-height: 120%;
      letter-spacing: 0.05em;
      transform: translateY(5%);
    }
  }
}

.set{
  padding-bottom: 21.5em;
  @media screen and (min-width: 577px){
    .bestseller__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
    .product-card:before{
      width: 40.625em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-top: 0;
  }

  .main-slider{
    .swiper{
      &-pagination{
        bottom: 1em;
      }
    }
  }

  .info-block{
    padding-bottom: .625em;
  }

  .bestseller{
    margin-top: 0;
    padding: 1em 0 1.5em;
    &__grid{
      padding: 0;
      margin-top: 2em;
    }
  }

  .product-card{
    font-size: .75em;
    &__img{
      margin-bottom: 2em;
    }
    &__title{
      font-size: 1.75em;
      padding: 0;
    }
  }

  .set{
    padding-bottom: 6em;
  }
}


@media screen and (max-width: 576px){
  .bestseller{
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
  }

  .product-card{
    font-size: 1em;
    max-width: 17.5em;
    width: 100%;
    margin: 0 auto;
    &:before{
      width: 20em;
      //height: 148%;
      //left: -1.25em;
    }
    &__img{
      margin-bottom: .75em;
    }
    &__title{
      font-size: .75em;
      br{
        display: none;
      }
    }
    &__bottom{
      padding-top: .375em;
    }
    &__price{
      font-size: 1em;
    }
    &__btn{
      padding: 0 .5em;
      height: 1.375em;
      span{
        font-size: .625em;
      }
    }
  }
}
