.footer{
  padding: 5em 0;
  background-image: url(../img/footer-bg.webp);
  background-size: 100% auto;
  background-position: center top;
  margin-top: -5em;
  &__inner{
    padding-top: 3.75em;
    border-top: 1px solid #fff;
  }
  &__wrap{
    width: 66.3125em;
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  &__col3{
    max-width: 15em;
  }
  &__logo{
    display: block;
    width: 18.125em;
    margin: 5em auto 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 2em;
    line-height: 119%;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__links{
    margin-top: .75em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1em;
    line-height: 125%;
    @include lastM(0 0 .875em);
    font-family: $font2;
  }
  &__desc{
    font-size: 1em;
    line-height: 125%;
    margin-top: .875em;
    max-width: 18.875em;
  }
  .socials{
    margin-top: 2em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.5em 0;
    margin: 0;
    &__logo{
      width: 12em;
    }
    &__inner{
      padding-top: 1.5em;
    }
    &__wrap{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
    &__col{
      order: 2;
    }
    &__col4{
      order: 3;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    background-size: cover;
    background-image: unset ;
    &__col,
    &__col2,
    &__col3,
    &__col4{
      font-size: .5em;
    }
    &__logo{
      margin-top: 1.875em;
      width: 5.5em;
    }
  }
}
