.advantages{
  margin-top: -15em;
  position: relative;
  z-index: 2;
  background-image: url(../img/advantages-bg.webp);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: center top 12em;
  padding-bottom: 4em;
  &:after{
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000 96.5%), linear-gradient(180deg, #000 0%, rgba(0, 0, 0, 0) 100%);;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 120%;
  }
  &__list{
    padding: 5em 2.5em 0;
    @include grid100gap(2em);
    li{
      font-size: 2.25em;
      line-height: 120%;
      padding-left: .75em;
      position: relative;
      &:before{
        content: 'â€¢';
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}

.media{
  margin-top: -3px;
  padding: 10.375em 0 11.25em;
  background-image: url(../img/media-bg.webp);
  background-size: cover;
  background-position: center;
  .container{
    position: relative;
    z-index: 2;
  }
  &__img{
    video,
    img{
      width: 100%;
      display: block;
    }
  }
}

.history{
  padding: 3.5em 0 12.5em;
  &__text{
    width: 66.3125em;
    max-width: 100%;
    margin: 2.5em auto 0;
    font-weight: 700;
    @include grid100gap(2.25em);
    p{
      font-size: 2em;
      line-height: 128%;
    }
  }
}

.gallery{
  width: 66.3125em;
  max-width: 100%;
  margin: 2em auto 0;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.5em));
  gap: 2em;
  .gallery-item{
    &:nth-child(1){
      grid-column: 1/-1;
    }
  }
}

.gallery-item{
  &__img{
    display: block;
    @include imgCover(66.7%);
  }
}


@media screen and (max-width: 992px){
  .advantages{
    margin-top: -2em;
    padding-bottom: 1.5em;
    &__img{
      bottom: 0;
      transform: translateY(25%);
    }
    &__desc{
      font-size: 1em;
    }
    &__list{
      padding: 1.25em 0;
      gap: 1em;
      li{
        font-size: 1.25em;
      }
    }
  }

  .media{
    padding: 1.5em 0;
  }

  .history{
    padding: 2em 0 4em;
    &__text{
      font-size: .75em;
    }
  }

  .gallery{
    grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
    gap: .75em;
  }
}


@media screen and (max-width: 576px){
  .advantages{
    padding-bottom: 1em;
    &__desc{
      font-size: .75em;
    }
    &__list{
      padding: .875em 0;
      gap: .5em;
      li{
        font-size: .5em;
      }
    }
  }

  .bestseller,
  .advantages,
  .media{
    background-image: unset;
    &:after{
      display: none;
    }
  }

  .media{
    padding: 1em 0;
  }

  .history{
    &__text{
      font-size: .25em;
      width: 100%;
      padding: 0 3em;
    }
  }

  .gallery{
    margin-top: 1em;
  }
}
