.header{
  background-image: url(../img/header-bg.jpg);
  background-size: cover;
  background-position: center;
  &__inner{
    padding: 2.5em 0 3em;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid #fff;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 18.5625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  width: 100%;
  margin-top: 5em;
}

.nav{
  ul{
    display: flex;
    justify-content: space-around;
    font-weight: 700;
    a{
      text-transform: uppercase;
      font-size: 2em;
      line-height: 119%;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      padding: 1em 0;
      flex-direction: row;
      justify-content: center;
      position: relative;
    }
    &__logo{
      width: 5.25em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    margin: 0;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    padding: 5em 1em 1em;
    z-index: 10;
  }

  .nav{
    ul{
      @include grid100gap(1em);
      a{
        font-size: 1.75em;
      }
    }
  }

  .burger{
    z-index: 11;
    position: absolute;
    width: 1em;
    height: 1em;
    display: flex;
    left: .875em;
    top: 50%;
    transform: translateY(-50%);
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    background-image: url(../img/header-bg2.jpg);
  }

  .nav{
    font-size: .75em;
  }
}
